<script>/* eslint-disable */</script>
<template>
<div class="main">
  <BarLoader style="margin: 0 auto;" color="#eee" class="mt-5" v-if="loading"></BarLoader>
  <v-container fluid fill-height class="my-5">
    <v-layout justify-center align-center>
      <v-flex xs12 sm8 md5 >
        <v-card
            v-if="jwt"
            max-width="600"
            color="grey darken-4"
            dark
            :class="{'my-3 pa-1': $vuetify.breakpoint.smAndDown, 'ma-3 px-1 py-0': $vuetify.breakpoint.smAndUp}"
            elevation="10"
          >
            <v-card-title class="title">
              Nouveau sujet
              <v-spacer></v-spacer>
              <CreateDialog></CreateDialog>
            </v-card-title>
          </v-card>
        
        <SubjectCard v-bind="subject" :editable="true" v-for="subject in getAll" :key="subject.id"></SubjectCard>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>
<style>
#intro {
  line-height: 20px;
}

a {
  text-decoration: none;
  color: #2c8ef3;
}
</style>
<script>
import DeleteDialog from '@/components/dialog/subject/delete.vue'
import EditDialog from '@/components/dialog/subject/edit.vue'
import CreateDialog from '@/components/dialog/subject/create.vue'
import SubjectCard from '@/components/card/subject.vue'
import { mapGetters } from 'vuex'
import { BarLoader } from '@saeris/vue-spinners'

export default {
  components: {
    SubjectCard,
    CreateDialog,
    DeleteDialog,
    EditDialog,
    BarLoader
  },
  computed: {
    ...mapGetters('jwt', [
      'jwt'
    ]),
    ...mapGetters('subjects', [
      'getAll',
      'loading'
    ])
  },
  created () {
    if (this.$route.meta.background) {
      document.body.setAttribute('class', 'bg' + this.$route.meta.background)
    }
    window.scrollTo(0, 0)
    this.$store.dispatch('subjects/fetchSubjects')
  }
}

</script>
